<template>
<v-list-tile @click="openFileDialog">
  <v-list-tile-title>
    <slot></slot>
  </v-list-tile-title>
  <v-dialog v-model="showFileDialog" max-width="290">
    <v-card>
      <v-card-title>
        <span class="headline">
          Datei <span class="text-lowercase"><slot></slot></span>
        </span>
      </v-card-title>
      <v-card-text>
        <v-text-field
          label="Wählen Sie eine Datei..."
          @click='pickFile'
          v-model='fileName'
          prepend-icon='storage'
          >
        </v-text-field>
        <input
          type="file"
          style="display: none"
          ref="fileinput"
          accept="application/json"
          @change="selectFile"
          >
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" flat @click="showFileDialog = false">Abbrechen</v-btn>
        <v-btn color="blue darken-1" flat @click="chooseFile">
          <slot></slot>
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</v-list-tile>
</template>

<script>
export default {
  data() {
    return {
      file: null,
      fileName: '',
      showFileDialog: false,
    };
  },
  methods: {
    openFileDialog() {
      setTimeout(() => {
        this.showFileDialog = true;
      });
    },
    pickFile() {
      this.$refs.fileinput.click();
    },
    selectFile(event) {
      const { files } = event.target;
      if (files) {
        this.fileName = files[0].name;
        [this.file] = files;
      }
    },
    chooseFile() {
      this.showFileDialog = false;
      this.$emit('input', { file: this.file, tag: this.tag });
    },
  },
  props: {
    tag: String,
  },
};
</script>
