<template>
<v-toolbar-title>
  <v-menu bottom left>
    <template v-slot:activator="{ on }">
      <v-btn v-on="on" icon>
        <v-icon>more_vert</v-icon>
      </v-btn>
    </template>

    <v-list>
      <v-list-tile @click="newList">
        <v-list-tile-title>Neu</v-list-tile-title>
      </v-list-tile>
      <ChooseFile
        tag="openFile"
        @input="readFile($event)"
        >
        Öffnen
      </ChooseFile>
      <ChooseFile
        tag="importFile"
        @input="readFile($event)"
        >
        Importieren
      </ChooseFile>
      <v-list-tile @click="saveFile">
        <v-list-tile-title>Speichern</v-list-tile-title>
      </v-list-tile>
      <v-list-tile @click="exportXLSX">
        <v-list-tile-title>Als Excel speichern</v-list-tile-title>
      </v-list-tile>
    </v-list>
  </v-menu>

  <v-dialog v-model="newListDialog" max-width="290">
    <v-card>
      <v-card-title>
        <span class="headline">Neue Liste</span>
      </v-card-title>
      <v-card-text>
        Wollen Sie die bestehende Liste tatsächlich löschen?
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" flat @click="newListDialog = false">Abbrechen</v-btn>
        <v-btn color="blue darken-1" flat @click="clearList">Ja</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</v-toolbar-title>
</template>

<script>
import { saveAs } from 'file-saver';
import { format } from 'date-fns';
import { Trs, scoreKeys, clinicalSelects } from '../common';
import makeXLSX from '../xlsx';
import ChooseFile from '../components/ChooseFile.vue';

function s2ab(s) {
  const buf = new ArrayBuffer(s.length);
  const view = new Uint8Array(buf);
  for (let i = 0; i < s.length; i += 1) {
    // eslint-disable-next-line
    view[i] = s.charCodeAt(i) & 0xFF;
  }
  return buf;
}

function importEntry(entry) {
  const importedEntry = new Trs();
  const importMap = {
    st: [0, 0, 0, 1, 2],
    md: [0, 0, 1, 2],
    wm: [0, 1, 2, 3],
    vh: [0, 1, 2],
    or: [0, 1, 2],
    mo: [0, 0, 0, 1, 2, 2, 3, 3],
    ak: [0, 0, 1, 3, 3, 3, 3, 3],
    be: [],
  };
  importedEntry.a.lab = entry.a.lab;
  importedEntry.a.diagnosis = entry.a.diagnosis;
  importedEntry.a.date = entry.a.date;
  importedEntry.a.dateofbirth = entry.a.dateofbirth;
  importedEntry.a.dateofaccident = entry.a.dateofaccident;
  importedEntry.a.phase = entry.a.phase;

  scoreKeys.forEach((element) => {
    if (element in entry.a) {
      if (Array.isArray(entry.a[element])) {
        const a = [];
        entry.a[element].forEach((arrayElement) => {
          a.push(parseInt(arrayElement, 10));
        });
        importedEntry.a[element] = a;
      } else if (clinicalSelects[element].multiple) {
        importedEntry.a[element] = [];
      } else {
        importedEntry.a[element] = parseInt(entry.a[element], 10);
      }
    }
  });
  if ('ms' in entry) {
    Object.keys(importMap).forEach((key) => {
      if (key === 'ak') {
        importedEntry.a.ak = importMap[key][entry.ms.length];
        if (entry.ms.length === 2 &&
            (entry.ms[0].ge > 2 || entry.ms[1].ge > 2)) {
          importedEntry.a.ak = 2;
        }
      } else if (key === 'be') {
        const badLegs = entry.ms.filter(item => (item.region > 4 && item.be === 2));
        if (badLegs.length === 2) {
          importedEntry.a.be = 3;
        } else if (badLegs.length === 1) {
          importedEntry.a.be = 1;
          const badTrunk = entry.ms.filter((item => item.region <= 4 && item.be === 2));
          if (badTrunk.length > 0) {
            importedEntry.a.be = 2;
          }
        }
      } else {
        importedEntry.a[key] = importMap[key][entry.a[key]];
      }
    });
  }
  return importedEntry;
}

export default {
  data() {
    return {
      file: null,
      fileName: '',
      openFileDialog: false,
      newListDialog: false,
    };
  },
  computed: {
    dateString() {
      return format(new Date(), 'YYYYMMDDHHmm');
    },
  },
  methods: {
    readFile(event) {
      if (event.file) {
        const reader = new FileReader();
        reader.addEventListener('loadend', () => {
          const dump = JSON.parse(reader.result);
          dump.forEach((fileEntry) => {
            this.$store.commit('push', importEntry(fileEntry));
          });
        });
        if (event.tag === 'openFile') {
          this.$store.commit('clear');
        }
        reader.readAsText(event.file);
        this.$router.push({ name: 'list' });
      }
    },
    saveFile() {
      const blob = new Blob(
        [JSON.stringify(this.$store.state.list, null, 2)],
        { type: 'application/json' },
      );
      saveAs(blob, `trs_${this.dateString}.json`);
    },
    newList() {
      if (this.$store.state.list.length > 0) {
        this.newListDialog = true;
      }
    },
    clearList() {
      this.$store.commit('clear');
      this.newListDialog = false;
      this.$router.push({ name: 'list' });
    },
    exportXLSX() {
      const output = makeXLSX(this.$store.state.list);
      saveAs(
        new Blob(
          [s2ab(output)],
          {
            type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
          },
        ),
        `trs_${this.dateString}.xlsx`,
      );
    },
  },
  components: {
    ChooseFile,
  },
};
</script>
