import Vue from 'vue';
import Vuex from 'vuex';
import VuexPersistence from 'vuex-persist';
import { cloneTrs } from './common';

Vue.use(Vuex);

const vuexLocal = new VuexPersistence({
  storage: window.localStorage,
});

export default new Vuex.Store({
  state: {
    list: [],
    lastVisited: 0,
    pagination: {
      descending: false,
      page: 1,
      rowsPerPage: -1,
      sortBy: '',
      totalItems: 0,
    },
  },
  mutations: {
    push(state, obj) {
      state.list.push(obj);
    },
    set(state, payload) {
      Vue.set(state.list, payload.index, payload.obj);
    },
    removeElement(state, index) {
      state.list.splice(index, 1);
    },
    cloneElement(state, index) {
      state.list.splice(index + 1, 0, cloneTrs(state.list[index]));
    },
    clear(state) {
      state.list = [];
    },
    visit(state, id) {
      state.lastVisited = id;
    },
    updatePagination(state, pagination) {
      state.pagination = pagination;
    },
  },
  plugins: [vuexLocal.plugin],
  strict: process.env.NODE_ENV !== 'production',
});
