import { differenceInYears } from 'date-fns';
import { Trs } from './common';

export default {
  computed: {
    trs: {
      get() {
        let { id } = this;
        if (id >= this.$store.state.list.length) {
          const newTrs = new Trs();
          this.$store.commit('push', newTrs);
          id = this.$store.state.list.length - 1;
        }
        this.$store.commit('visit', id);
        return this.$store.state.list[id];
      },
      set(value) {
        this.$store.commit('set', { index: this.id, obj: value });
      },
    },
    patientAge() {
      if (this.trs.a.dateofbirth) {
        const [day, month, year] = this.trs.a.dateofbirth.split('.');
        if (day && month && year && year.length === 4) {
          const date = new Date(year, month - 1, day - 1, 12, 0);
          return differenceInYears(new Date(), date);
        }
      }
      return null;
    },
  },
};
