<template>
<v-layout xs12 column>
  <v-card flat>
    <v-card-text>
      <v-alert
        v-model="alert"
        dismissible
        outline
        type="info"
        >
        Der Eintrag wurde Dupliziert. Bitte ändern Sie die Kennung.
      </v-alert>
      <v-layout row wrap>
        <v-flex xs12 sm4>
          <v-text-field
            label="Kennung"
            ref="label"
            :value="trs.a.lab"
            @input="assign('lab', $event)"
            >
          </v-text-field>
        </v-flex>
        <v-flex xs12 sm4>
          <v-menu
            :close-on-content-click="false"
            v-model="menu"
            :nudge-right="40"
            lazy
            transition="scale-transition"
            offset-y
            full-width
            min-width="290px"
            >
            <v-text-field
              slot="activator"
              :value="trs.a.date"
              label="Datum"
              prepend-icon="event"
              @input="dateInputHelper('date', $event)"
              ></v-text-field>
            <v-date-picker
              :value="parseDate(trs.a.date)"
              no-title
              @input="assignClose('date', formatDate($event))"
              locale="de-de"
              first-day-of-week="1"
              >
            </v-date-picker>
          </v-menu>
        </v-flex>
        <v-flex xs12 sm4>
          <v-text-field
            :value="trs.a.dateofaccident"
            @input="dateInputHelper('dateofaccident', $event)"
            label="Unfalltag"
            prepend-icon="event"
            hint="Format TT.MM.JJJJ"
            ></v-text-field>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs12 sm4>
          <v-select
            :value="trs.a.phase"
            @input="assign('phase', $event)"
            :items="phaseOptions"
            label="Klinisch Reha"
            ></v-select>
        </v-flex>
        <v-flex xs12 sm4>
          <v-text-field
            :value="trs.a.dateofbirth"
            @input="dateInputHelper('dateofbirth', $event)"
            label="Geburtsdatum"
            prepend-icon="event"
            hint="Format TT.MM.JJJJ"
            ></v-text-field>
        </v-flex>
        <v-flex xs12 sm4>
          <v-text-field
            :value="patientAge"
            label="Alter"
            prepend-icon="none"
            readonly
            ></v-text-field>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs12>
          <v-textarea
            label="Diagnose(n)"
            :value="trs.a.diagnosis"
            @input="assign('diagnosis', $event)"
            hint="Eine Zeile pro Diagnose"
            ></v-textarea>
        </v-flex>
      </v-layout>
    </v-card-text>
  </v-card>
</v-layout>
</template>

<script>
import trsMixin from '../TrsMixin';
import { cloneTrs } from '../common';

export default {
  data() {
    return {
      alert: this.cloned,
      menu: false,
      phaseSelected: null,
      phaseOptions: [
        'Phase A',
        'Phase B',
        'Phase C',
        'Phase D',
        'Phase E',
        'Phase F',
      ],
    };
  },
  mixins: [trsMixin],
  props: {
    id: Number,
    cloned: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    save(date) {
      this.$refs.menu.save(date);
    },
    assign(key, value) {
      const newTrs = cloneTrs(this.trs);
      newTrs.a[key] = value;
      this.trs = newTrs;
    },
    assignClose(key, value) {
      this.menu = false;
      this.assign(key, value);
    },
    dateInputHelper(key, value) {
      if (value.match(/^\d\d$/) || value.match(/^\d\d\.\d\d$/)) {
        value += '.';
      }
      this.assign(key, value);
    },
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${day}.${month}.${year}`;
    },
    parseDate(date) {
      if (!date) return null;

      const [day, month, year] = date.split('.');
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`;
    },
  },
  mounted() {
    if (this.cloned) {
      this.$refs.label.focus();
    }
  },
};
</script>
