import Vue from 'vue';
import Router from 'vue-router';
import TrsList from './views/TrsList.vue';
import TrsDetail from './views/TrsDetail.vue';

Vue.use(Router);

function cast(strId) {
  const id = parseInt(strId, 10);
  if (Number.isNaN(id)) {
    return 0;
  }
  return id;
}

function detailProps(route) {
  return { id: cast(route.params.id), cloned: false };
}

function clonedProps(route) {
  return { id: cast(route.params.id), cloned: true };
}

export default new Router({
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'list',
      component: TrsList,
    },
    {
      path: '/detail/:id',
      name: 'detail',
      component: TrsDetail,
      props: detailProps,
    },
    {
      path: '/cloned/:id',
      name: 'cloned',
      component: TrsDetail,
      props: clonedProps,
    },
  ],
  scrollBehavior(to) {
    if (to.name === 'list') {
      return { selector: '#selected' };
    }
    return { x: 0, y: 0 };
  },
});
