// -*-  mode: javascript; js-indent-level: 2; -*-
import XLSX from 'xlsx';
import { scores, scoreKeys } from './common';

function fixRow(cstr) { return cstr.replace(/([A-Z]|^)(\d+)$/, '$1$$$2'); }
function fixCol(cstr) { return cstr.replace(/^([A-Z])/, '$$$1'); }
function fixCell(cstr) { return fixCol(fixRow(cstr)); }

function insertScoreCell(ws, col, row, scoreElement, index) {
  let scoreRef;
  const cellRef = XLSX.utils.encode_cell({ c: col, r: row });
  const form = [];
  if (Array.isArray(index)) {
    form.push(0);
    index.forEach((member) => {
      scoreRef = XLSX.utils.encode_cell({
        c: parseInt(member, 10) + 1,
        r: scoreKeys.indexOf(scoreElement) + 1,
      });
      form.push(fixCell(scoreRef));
    });
  } else {
    scoreRef = XLSX.utils.encode_cell({
      c: parseInt(index, 10) + 1,
      r: scoreKeys.indexOf(scoreElement) + 1,
    });
    form.push(fixCell(scoreRef));
  }
  ws[cellRef] = { f: form.join('+'), t: 'n' };
}

function parseDate(dateString) {
  const dateParser = /(\d{1,2})\.(\d{1,2})\.(\d{2,4})/;
  const match = dateString.match(dateParser);
  if (match) {
    return new Date(
      match[3], // year
      match[2] - 1, // monthIndex
      match[1], // day
    ).toISOString();
  }
  return null;
}

export default function (trsList) {
  const table = [];
  const heading1 = [];
  const heading2 = [
    'Kennung',
    'Klinisch Reha',
    'Datum',
    'Geburtsdatum',
    'Alter',
    'Unfalltag',
    'Score',
  ].concat(scoreKeys);

  table.push(['Score Komponenten']);
  scoreKeys.forEach((key) => {
    table.push([key].concat(scores[key]));
  });
  table.push([]);
  Array.prototype.push.apply(
    heading2,
    [
      'Diagnose 1',
      'Diagnose 2',
      'Diagnose 3',
      'Diagnose 4',
      'Diagnose 5',
    ],
  );
  table.push(heading1);
  table.push(heading2);
  let maxCol = heading2.length - 1;
  const ws = XLSX.utils.aoa_to_sheet(table);
  let row = Object.keys(scores).length + 4;
  let col;
  let scoreCol;
  let birthRef;
  trsList.forEach((trs) => {
    col = 0;
    let cellRef = XLSX.utils.encode_cell({ c: col, r: row });
    ws[cellRef] = { v: trs.a.lab, t: 's' };
    col += 1;
    cellRef = XLSX.utils.encode_cell({ c: col, r: row });
    ws[cellRef] = { v: trs.a.phase, t: 's' };
    col += 1;
    if (trs.a.date) {
      cellRef = XLSX.utils.encode_cell({ c: col, r: row });
      ws[cellRef] = { v: parseDate(trs.a.date), t: 'd' };
    }
    col += 1;
    if (trs.a.dateofbirth) {
      cellRef = XLSX.utils.encode_cell({ c: col, r: row });
      ws[cellRef] = { v: parseDate(trs.a.dateofbirth), t: 'd' };
    }
    col += 1;
    birthRef = cellRef;
    if (trs.a.dateofbirth) {
      cellRef = XLSX.utils.encode_cell({ c: col, r: row });
      ws[cellRef] = { f: `DATEDIF(${birthRef},TODAY(),"y")`, t: 'n' };
    }
    col += 1;
    if (trs.a.dateofaccident) {
      cellRef = XLSX.utils.encode_cell({ c: col, r: row });
      ws[cellRef] = { v: parseDate(trs.a.dateofaccident), t: 'd' };
    }
    scoreCol = col + 1;
    col += 2;
    scoreKeys.forEach((element) => {
      insertScoreCell(ws, col, row, element, trs.a[element]);
      col += 1;
    });
    cellRef = XLSX.utils.encode_cell({ c: col, r: row });
    let diagCol = col;
    if (trs.a.diagnosis) {
      trs.a.diagnosis.split('\n').forEach((diagnosis) => {
        cellRef = XLSX.utils.encode_cell({ c: diagCol, r: row });
        ws[cellRef] = { v: diagnosis, t: 's' };
        maxCol = Math.max(maxCol, diagCol);
        diagCol += 1;
      });
    }
    const range = XLSX.utils.encode_range({
      s: { c: scoreCol + 1, r: row },
      e: { c: col - 1, r: row },
    });
    cellRef = XLSX.utils.encode_cell({ c: scoreCol, r: row });
    ws[cellRef] = { f: `100 - SUM(${range})`, t: 'n' };
    row += 1;
  });
  ws['!ref'] = XLSX.utils.encode_range({
    s: { c: 0, r: 0 },
    e: { c: maxCol, r: row },
  });
  const wb = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(wb, ws, 'OTRS');
  const wopts = {
    bookType: 'xlsx',
    bookSST: false,
    type: 'binary',
    cellDates: true,
  };
  return XLSX.write(wb, wopts);
}
