<template>
<v-container fluid>
  <v-data-table
    :headers="computedHeaders"
    :items="rows"
    class="elevation-1"
    no-data-text="Keine Einträge"
    :disable-initial-sort="true"
    :pagination.sync="pagination"
    >
    <template slot="items" slot-scope="props">
      <tr :class="{ 'blue lighten-5': props.item.selected }">
        <td
          v-if="!$vuetify.breakpoint.xs"
          class="text-xs-right"
          @click="$router.push({ name: 'detail', params: { id: props.item.id } })"
          >
          {{ (pagination.page - 1) * pagination.rowsPerPage + props.index + 1 }}
        </td>
        <td
          @click="$router.push({ name: 'detail', params: { id: props.item.id } })"
          >
          <span id="selected" v-if="props.item.selected"></span>
          {{ props.item.label || `Nr. ${props.item.id}` }}
        </td>
        <td
          v-if="!$vuetify.breakpoint.mdAndDown"
          @click="$router.push({ name: 'detail', params: { id: props.item.id } })"
          >
          {{ formatDate(props.item.dateofbirth) }}
        </td>
        <td
          v-if="!$vuetify.breakpoint.smAndDown"
          @click="$router.push({ name: 'detail', params: { id: props.item.id } })"
          >
          {{ props.item.diag1 }}
        </td>
        <td
          v-if="!$vuetify.breakpoint.mdAndDown"
          @click="$router.push({ name: 'detail', params: { id: props.item.id } })"
          >
          {{ props.item.diag2 }}
        </td>
        <td
          v-if="!$vuetify.breakpoint.lgAndDown"
          @click="$router.push({ name: 'detail', params: { id: props.item.id } })"
          >
          {{ props.item.diag3 }}
        </td>
        <td
          v-if="!$vuetify.breakpoint.mdAndDown"
          @click="$router.push({ name: 'detail', params: { id: props.item.id } })"
          >
          {{ formatDate(props.item.date) }}
        </td>
        <td
          v-if="!$vuetify.breakpoint.xs"
          class="text-xs-right"
          @click="$router.push({ name: 'detail', params: { id: props.item.id } })"
          >
          {{ props.item.score }}
        </td>
        <td class="text-xs-right">
          <v-icon
            small
            @click="cloneGoTo(props.item.id)"
            >
            file_copy
          </v-icon>
        </td>
        <td class="text-xs-right">
          <v-icon
            small
            @click="removeElement(props.item.id)"
            >
            clear
          </v-icon>
        </td>
      </tr>
    </template>
  </v-data-table>
  <v-btn
    color="blue"
    class="white--text my-3"
    :to="`/detail/${length}`"
    >
    <v-icon left dark>add_circle</v-icon>
    <span>Neuer Eintrag</span>
  </v-btn>
</v-container>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { format } from 'date-fns';
import { calculateScore } from '../common';

export default {
  data() {
    return {
      headers: [
        {
          text: 'Zeile', value: 'id', sortable: false, hide: 'xs',
        },
        { text: 'Kennung', value: 'label' },
        { text: 'Geburtsdatum', value: 'dateofbirth', hide: 'mdAndDown' },
        { text: 'Diagnose 1', value: 'diag1', hide: 'smAndDown' },
        { text: 'Diagnose 2', value: 'diag2', hide: 'mdAndDown' },
        { text: 'Diagnose 3', value: 'diag3', hide: 'lgAndDown' },
        { text: 'Datum', value: 'date', hide: 'mdAndDown' },
        { text: 'Score', value: 'score', hide: 'xs' },
        { text: 'Duplizieren', value: 'id', sortable: false },
        { text: 'Entfernen', value: 'id', sortable: false },
      ],
    };
  },
  methods: {
    ...mapMutations([
      'removeElement',
    ]),
    cloneGoTo(id) {
      this.$store.commit('cloneElement', id);
      this.$router.push({
        name: 'cloned',
        params: { id: id + 1, cloned: true },
      });
    },
    parseDate(date) {
      const [day, month, year] = date.split('.');
      if (day && month && year) {
        return new Date(year, month - 1, day);
      }
      return null;
    },
    formatDate(date) {
      if (date) {
        return format(date, 'DD.MM.YYYY');
      }
      return null;
    },
  },
  computed: {
    computedHeaders() {
      return this.headers.filter(
        h => !h.hide || !this.$vuetify.breakpoint[h.hide],
      );
    },
    ...mapState({
      lastVisited: 'lastVisited',
    }),
    length() {
      return this.$store.state.list.length;
    },
    rows() {
      const rows = [];
      this.$store.state.list.forEach((element, index) => {
        const row = {};
        row.id = index;
        row.label = element.a.lab;
        row.dateofbirth = this.parseDate(element.a.dateofbirth);
        [row.diag1, row.diag2, row.diag3] = element.a.diagnosis.split('\n');
        row.date = this.parseDate(element.a.date);
        row.score = calculateScore(element);
        row.selected = index === this.lastVisited;
        rows.push(row);
      });
      return rows;
    },
    pagination: {
      get() {
        return this.$store.state.pagination;
      },
      set(pagination) {
        this.$store.commit('updatePagination', pagination);
      },
    },
  },
};
</script>

<style>
.v-btn {
    text-transform: none;
}
</style>
