<template>
<v-layout column mb-3 class="text-xs-center">
  <v-flex>
  <v-dialog
    v-model="dialog"
    fullscreen hide-overlay lazy
    transition="dialog-bottom-transition"
    >
    <v-btn color="primary" mb-3 slot="activator" dark>
      <v-icon left dark>print</v-icon>
      <span>Druckvorschau</span>
    </v-btn>
    <v-card>
      <v-toolbar dark color="primary">
        <v-btn icon dark @click="dialog = false">
          <v-icon>close</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-toolbar-items>
          <v-btn dark flat @click="print()">
            <v-icon left dark>print</v-icon>
            Drucken
          </v-btn>
        </v-toolbar-items>
      </v-toolbar>
      <div ref="printArea">
        <v-container grid-list-lg>
          <h3>Trauma-Reha-Score </h3>
          <h1>{{ trs.a.lab }}</h1>
          <p v-if="trs.a.date">Datum: {{ trs.a.date }}</p>
          <div v-if="trs.a.dateofbirth">
            <p>Geburtsdatum: {{ trs.a.dateofbirth }}</p>
            <p>Alter: {{ patientAge }} Jahre</p>
          </div>
          <p v-if="trs.a.dateofaccident">Unfalltag: {{ trs.a.dateofaccident }}</p>
          <p v-if="trs.a.diagnosis">Diagnose(n): {{ trs.a.diagnosis }}</p>
          <DisplayScore :id="id" print />
          <div v-for="(area, key) in legend" :key="key">
            <v-layout row wrap>
              <v-flex offset-xs4 xs8 mt-5>
                <h3>{{ scoreAreas[key].name }}</h3>
              </v-flex>
            </v-layout>
            <v-layout v-for="(item, index) in area" :key="'a' + index" row>
              <v-flex class="font-weight-bold" xs4>
                {{ item.label }}
              </v-flex>
              <v-flex xs8>
                <div v-if="Array.isArray(item.selected)">
                  <div v-for="(selection, index) in item.selected" :key="index">
                    {{ selection }}<br />
                  </div>
                </div>
                <div v-else v-html="item.selected"></div>
              </v-flex>
            </v-layout>
            <v-layout
              v-for="(footnote, index) in scoreAreas[key].footnotes"
              :key="'b' + index"
              row
              >
              <v-flex class="caption" xs12>{{ footnote }} </v-flex>
            </v-layout>
          </div>
        </v-container>
      </div>
    </v-card>
  </v-dialog>
  </v-flex>
</v-layout>
</template>

<script>
import { Printd } from 'printd';
import trsMixin from '../TrsMixin';
import { clinicalSelects, scoreAreas, getScoreArea } from '../common';
import DisplayScore from './DisplayScore.vue';

function appendLegend(legend, key, value) {
  if (key in clinicalSelects) {
    let selected;
    if (Array.isArray(value)) {
      selected = value.map(v => clinicalSelects[key].options[v]);
    } else {
      selected = clinicalSelects[key].options[value];
    }
    if (key === 'ua') {
      if ((Array.isArray(value) && value.length === 0) || value === 0) {
        selected = 'Selbstständig';
      }
    }
    const areaKey = getScoreArea(key);
    legend[areaKey].push({
      label: `${clinicalSelects[key].label} (${key.toUpperCase()})`,
      selected,
    });
  }
}

export default {
  data() {
    return {
      dialog: false,
      scoreAreas,
    };
  },
  computed: {
    legend() {
      const legend = {};
      Object.keys(scoreAreas).forEach((key) => {
        legend[key] = [];
      });
      Object.entries(this.trs.a).forEach(
        ([key, value]) => {
          appendLegend(legend, key, value);
        },
      );
      return legend;
    },
  },
  mixins: [trsMixin],
  props: {
    id: Number,
  },
  methods: {
    print() {
      this.d.print(
        this.$refs.printArea,
        '',
        (win, doc, node, launchPrint) => {
          const styles = [].slice.call(document.getElementsByTagName('style'));
          styles.forEach(
            styleElement => doc.head.appendChild(styleElement.cloneNode(true)),
          );
          launchPrint(win);
          this.dialog = false;
        },
      );
    },
  },
  mounted() {
    this.d = new Printd();
  },
  components: {
    DisplayScore,
  },
};
</script>

<style>
@media print {
    body {
        width: 800px !important;
        font-family: sans-serif;
    }
}
</style>
