<template>
<tr>
  <td class="font-weight-bold text-xs-right">
    {{ displayArea.name }}
  </td>
  <template v-if="!$vuetify.breakpoint.smAndDown">
    <td
      :colspan="10 - displayArea.cols.length"
      class="font-weight-bold"
      v-if="10 - displayArea.cols.length > 0"
      >
    </td>
    <td v-for="col in displayArea.cols"
        :key="col.key"
        class="text-xs-right"
        >
      <span v-if="displayArea.cols.length > 1">
        {{ col.key }}: {{ col.score }}
      </span>
    </td>
  </template>
  <td class="text-xs-right">{{ displayArea.scoreSum }}</td>
  <td></td>
</tr>
</template>

<script>
import { scoreAreas, elementScore } from '../common';

export default {
  data() {
    return {
      scoreAreas,
    };
  },
  computed: {
    displayArea() {
      const displayArea = { name: this.area.name, cols: [], scoreSum: 0 };
      const keys = Object.keys(this.area.components);
      keys.forEach((key) => {
        const score = elementScore(key, this.values[key]);
        displayArea.cols.push({ key, score });
        displayArea.scoreSum += score;
      });
      return displayArea;
    },
  },
  props: {
    values: Object,
    area: Object,
    areaKey: String,
  },
};
</script>
